.App {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.buttons {
  margin-bottom: 1rem;
}
.buttons button {
  font-size: 1rem;
  padding: 1rem;
  margin: 0.3rem;
}
.list-item {
  background: #B08620;
  font-size: 3rem;
  padding: 1.5rem;
  border: solid 1px;
  color: #FEF0CD;
}
.big {
  font-size: 4rem;
  padding: 2.5rem;
  color: dimgrey;
  background: lightgray;
}

